      content: "";
      display: block;
      padding-top: 28.04878%;
}
.Period-counter .title {
      position: absolute;
      left: 0;
      bottom: 115%;
      -webkit-animation: fadeOut 1s 3s both;
              animation: fadeOut 1s 3s both;
}
.Period-counter .count {
      font-size: 1.2em;
      z-index: 1;
      position: absolute;
      left: 10px;
      top: 55%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.Period-counter .count span.dash {
        margin-right: 3px;
        margin-top: -3px;
}
.Period-counter svg {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
}
.Period-bar {
    z-index: 2;
    position: absolute;
    top: 0;
    right: 0;
    width: 5px;
    height: 100%;
}
.Period-bar:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      background-color: currentColor;
      opacity: .3;
}
.Period-bar .loading {
      background-color: currentColor;
      box-shadow: 0 0 4px rgba(255, 255, 255, 0.5);
}
@-webkit-keyframes fadeOut {
to {
    opacity: 0;
}
}
@keyframes fadeOut {
to {
    opacity: 0;
}
}
.t-enterFade-enter-active, .t-enterFade-leave-active {
  transition: .6s;
}
.t-enterFade-enter, .t-enterFade-leave-to {
  opacity: 0;
}



