
.Home {
  position: relative;
  height: 100%;
}
.Info {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 34px;
  color: white;
  background-color: #000;
}
.Info-link {
    font-size: .9em;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    letter-spacing: 1px;
}
.Info .moon {
    position: absolute;
    right: 7px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-size: 23px;
    width: 1em;
    height: 1em;
}
.Info .moon img {
      max-width: 100%;
}
.Times {
  position: absolute;
  left: 0;
  right: 0;
  top: 34px;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}



